header {
  width: 85vw;
  color: $secondary-text-color;
  margin: auto;
}

.logo {
  width: 15%;
  height: auto;
  vertical-align: middle; 
  margin-left: 30px; 
}

.main-nav {
  display: inline-block;
  width: 55%;
  margin: 0 auto;
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 2px;

  ul {
    display: inline-block;
    vertical-align: middle;  
    list-style: none;
    margin-left: 20%;
  }

  li {
    display: inline-block;
    margin-right: 20px;
  }

  a {
    color: $secondary-text-color;
    text-decoration: none;
  }

  a:hover {
    text-decoration: underline;
  }

}

.basket {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  padding-left: 44px;
  margin-left: 10%;
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 2px;

  &:after {
    content: "";
    background: url('../images/icons/basket.png') no-repeat;
    width: 27px;
    height: 24px;
    position: absolute;
    top: 0;
    left: 0;
  }

  a {
    color: $secondary-text-color;
  }

  span:hover {
    text-decoration: underline;
  }
}

.basket-amount {
  color: #b7b6b6;
}

