* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  @include font-config($main-fonts, 100, 28px);
}

h1,
h2,
h3 {
  @include font-config($heading-fonts, normal, 1);
  color: $heading-color;
}

h2 {
  font-size: 60px;
  margin-bottom: 35px;
}

h3 {
  font-size: 22px;
}

p, 
strong {
  color: $main-text-color;
  font-size: 18px;
}

section {
  max-width: 1170px;
  margin: 0 auto;
}