.subscribe-form {
  text-align: center;
  background-color: $form-bg-color; 
  margin: auto;
  padding: 85px 110px;

  form {
    margin: 50px auto 0;
  }
}

.button-email {
  line-height: inherit;
}

.email {
  width: 55%;
  padding: 21px;
  border: none;
  background-color: $input-color;
  margin-right: -4px; 

  &::placeholder {
    color: $placeholder-color;
    font-weight: 100; 
  }
}
